import 'babel-polyfill';

window.config = {};
/**
 *  if else 
 * env file index 
 *  node  config 
 * .env 
 */
if (process.env.NODE_ENV === 'development') {
  window.config = {
    __ENV__: 'development',
    __API_HOST__: '',
    __APP_NAME__: 'oauth_open',
  };
} else {
  // 
  window.config = {
    __ENV__: 'production',
    __API_HOST__: '',
    __APP_NAME__: 'oauth_open',
  };
}

class Adapter {
  constructor(config) {
    this.config = config;
  }
  // 
  reset() {
    const baseline =
      this.config && this.config.baseline && !isNaN(this.config.baseline)
        ? this.config.baseline
        : 750;
    let logicFontSize = (document.documentElement.clientWidth / baseline) * 100;
    logicFontSize = logicFontSize > 100 ? 100 : logicFontSize;
    document.documentElement.style.fontSize = `${logicFontSize}px`;

    // 
    const actualFontSize = parseFloat(
      window.getComputedStyle(document.documentElement).fontSize,
    );
    const scaleRate = logicFontSize / actualFontSize;
    if (scaleRate === 1) {
      return;
    }
    document.documentElement.style.fontSize = `${
      logicFontSize * scaleRate > 90 ? 90 : logicFontSize * scaleRate
    }px`;
  }
}

/**
 * ADAPTER_CONF
 * window.ADAPTER_CONF = {
 *      baseline: 750
 * };
 */

window.Adapter = new Adapter(window.ADAPTER_CONF || {});

window.Adapter.reset();

window.onload = () => {
  window.Adapter.reset();
};

window.onresize = () => {
  window.Adapter.reset();
};



